.modal {
    font-family: Arial, Helvetica, sans-serif;
    opacity: 0;
    visibility: hidden;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba($color: #000000, $alpha: 0.3);
    transition: 0.3s ease-in-out;

    &.active {
        opacity: 1;
        visibility: visible;
    }

    &__content {
        width: 500px;
        margin: 15% auto;
        background-color: #fff;
        border-radius: 5px;
        position: relative;
        transition: inherit;
        opacity: 0;
        visibility: hidden;
        transform: translateY(-100px);
        padding: 16px 20px;
        text-align: left;

        &.small {
            width: 350px;
            text-align: center;
        }
    }
    
    &__footer {
        display: flex;
        justify-content: space-between;
        margin-top: 10px;
        padding-top: 10px;

        &__secondary {
            padding-top: 10px;
            text-align: center;
            justify-content: center;
        }
    }

    &.active &__content {
        opacity: 1;
        visibility: visible;
        transform: translateY(0);
    }

    &__close {
        position: absolute;
        top: 16px;
        right: 20px;
        font-size: 1.5rem;
        cursor: pointer;
        color: #7f7f7f;

        &:hover {
            color: #000;
        }
    }

    &__button {
		padding: 10px;
        border: none;
        padding: 15px;
        text-align: center;
        text-decoration: none;
        display: inline-block;
        font-size: 16px;
        margin: 4px 2px;
        cursor: pointer;
        border-radius: 12px;
        background-color: #008CBA;
        color: #ffffff;
	}

    h2 {
        text-align: center;
    }
}

@media (max-width: 725px) {
	.modal {
		&__content {
            width: 75%;

            &.small {
                width: 60%;
            }
		}
	}
}